import React from "react";
import "./styles/app.scss";
import "./todo.css";
class Todo extends React.Component {
  constructor(props) {
    super(props);
    // this.state = { id: props.id, value: props.value, done: props.done };
    this.state = { data: props.data };
  }
  static getDerivedStateFromProps(props, state) {
    return { data: props.data };
  }
  // completeTask = () => {
  //   // this.setState({ done: true });
  //   this.props.completeTask(this.state.data.id);
  // };
  render() {
    let check = "";
    if (!this.state.done) {
      check = (
        <img
          src="./assets/icon/1930264_check_complete_done_green_success_icon.png"
          alt=""
          className="icon-check"
          style={{ cursor: "pointer" }}
          title="thêm task"
          onClick={(e) => {
            e.target.classList.add("show-done");
            this.props.completeTask(this.state.data.id);
          }}
        />
      );
    }
    return (
      <li className="list-item">
        <span className={this.state.data.done ? "noCheckDone pad-right" : "pad-right"}>
          {this.state.data.name}
        </span>
        <div className="handler">
          {check}
          <img
            src="./assets/icon/48785_delete_document_error_remove_icon.png"
            alt=""
            className="icon-delete"
            style={{ cursor: "pointer" }}
            title="thêm task"
            onClick={() => this.props.deleteTask(this.props.data.id)}
          />
        </div>
      </li>
    );
  }
}

export default Todo;
