import React from "react";
import "./styles/app.scss";
import "./App.css";
import Todo from "./todo";

class Todolist extends React.Component {
  constructor(props) {
    super(props);
    this.state = { title: props.name, taskName: "", tasks: [] };
  }
  myTaskChangeHandler = (e) => {
    this.setState({ taskName: e.target.value });
  };
  addTask = () => {
    if (this.state.taskName === "") {
      return;
    } else {
      const id = this.state.tasks.length;
      const name = this.state.taskName;
      this.state.tasks.push({ id, name, done: false });
      this.setState({ taskName: "" });
    }
  };
  deleteTask = (id) => {
    const tasks = this.state.tasks.filter((task) => task.id !== id);
    this.setState({ tasks });
  };
  completeTask = (id) => {
    // console.log(id);
    const tasks = this.state.tasks;
    tasks.forEach((task) => {
      if (task.id === id) {
        task.done = true;
      }
    });
    // console.log("taskh muon complete", tasks);
    this.setState({ tasks });
  };
  render() {
    //các key value mà thuộc tính con sở hữu
    // console.log(this.state);
    //lấy từ component cha xuống
    // console.log(this.props);

    return (
      <div className="App">
        <h1>{this.state.title}</h1>
        <img src="./assets/icon/1055091_clipboard_document_icon.png" alt="" className="small-bg" />

        <p>Nhập công việc cần làm:</p>
        <div className="flex">
          <input
            type="text"
            className="input"
            value={this.state.taskName}
            onChange={this.myTaskChangeHandler}
          />
          <img
            src={"./assets/icon/299068_add_sign_icon.png"}
            alt=""
            className="icon"
            style={{ cursor: "pointer" }}
            title="thêm task"
            onClick={this.addTask}
          />
        </div>
        <ul className="list">
          {this.state.tasks.map((value, index) => {
            // return (
            //   <li className="list-item" key={index}>
            //     {value}
            //   </li>
            // );
            return (
              <Todo
                ref="todo"
                key={value.id}
                data={value}
                deleteTask={this.deleteTask}
                completeTask={this.completeTask}
              />
            );
          })}
        </ul>
      </div>
    );
  }
}

export default Todolist;
